import { Grid, Link, Typography } from "@mui/material"
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

function ExperienceEntry(props) {

    return(
    <Grid sx={{pl: 5}}>
    <Typography variant='h6'>{props.data.role} {<Link href={props.data.url} underline='hover' color='primary'>@ {props.data.company}</Link>}</Typography>
    <Typography variant='subtitle'>{props.data.start} - {props.data.end}</Typography>
    {props.data.description.map(bullet => <Typography variant='body1'><ChevronRightIcon/>{bullet}</Typography>)}
    </Grid>)
}

export default ExperienceEntry